/**
 * Slide given element up or down based on direction
 *
 * @param {HTMLElement} el element to slide
 * @param {String} direction "up" or "down" - defaults to up
 */
export const slideToggle = (
  el: HTMLElement,
  direction: 'up' | 'down',
  offset: number,
) => {
  const customOffset = !isNaN(offset) ? offset : 0;

  if (direction === 'down') {
    el.style.maxHeight = 'none';
    const height = `${el.offsetHeight + customOffset}px`;
    el.style.maxHeight = '0';

    requestAnimationFrame(() => {
      el.style.maxHeight = height;
    });
  } else {
    const height = `${el.offsetHeight + customOffset}px`;
    el.style.maxHeight = height;

    requestAnimationFrame(() => {
      el.style.maxHeight = '0';
    });
  }
};
